import React from "react";
import PropTypes from "prop-types";

import StatefulInput from "../../../../commons/StatefulInput";
import { useHandleInput } from "../../../../../hooks/useHandleForm";
import { ADVANCED_CONFIG_SET_VALUE } from "../../../../../consts/configurations/configurationsActions";
import { useOAuthContext } from "../../../../../contexts/oauthContext";

const propTypes = {
  clientId: PropTypes.string,
  tokenUrl: PropTypes.string,
  clientSecret: PropTypes.string
};

const defaultProps = {
  clientId: "",
  tokenUrl: "",
  clientSecret: ""
};

const EnableOAuth = () => {
  const [{ clientId, tokenUrl, clientSecret }, dispatch] = useOAuthContext();
  const onBlur = useHandleInput(ADVANCED_CONFIG_SET_VALUE, dispatch);
  return (
    <div className="slds-grid slds-gutters_small">
      <div className="slds-col slds-size_1-of-3">
        <StatefulInput label="OAuth Client ID" value={clientId} id="clientId" onBlur={onBlur} />
      </div>
      <div className="slds-col slds-size_1-of-3">
        <StatefulInput
          label="OAuth Client Secret"
          value={clientSecret}
          id="clientSecret"
          onBlur={onBlur}
        />
      </div>
      <div className="slds-col slds-size_1-of-3">
        <StatefulInput label="OAuth Token URL" value={tokenUrl} id="tokenUrl" onBlur={onBlur} />
      </div>
    </div>
  );
};

EnableOAuth.propTypes = propTypes;
EnableOAuth.defaultProps = defaultProps;

export default EnableOAuth;
