import { QUICK_START_FORM_INITIAL_LOAD } from "../../consts/configurations/configurationsActions";
import { CLOUD_STORAGE_PROVIDERS_S3_COMPATIBLE } from "../../consts/configurations/cloudStorageProviders";
import quickStartFormReducer from "./quickStartFormReducer";

const storageReducer = (state, { type, payload: { oauth, ...omittedOauthPayload } }) => {
  let newState = state;
  let action = { type, payload: omittedOauthPayload };

  if (type === QUICK_START_FORM_INITIAL_LOAD) {
    if (action.payload.s3CompatibleEndpoint) {
      newState = {
        ...newState,
        provider: CLOUD_STORAGE_PROVIDERS_S3_COMPATIBLE
      };
    }

    const { clientId, clientSecret, tokenUrl } = oauth;
    if (clientId || clientSecret || tokenUrl) {
      action = { ...action, payload: { ...action.payload, enableOAuth: true } };
    }
  }

  return quickStartFormReducer(newState, action);
};

export default storageReducer;
